
















































































































































































































































































































































































































































#header{
  border-bottom:none ;
  position: relative;
  background: url("../../../assets/main_images/navBack.png") no-repeat;
  background-size: 100% 100%;
  height: 70px;
  /*logo*/
  .title {
    width: 25%;
    position: absolute;
    left: 20px;
    top: 50%;
    transform: translateY(-50%);
  }
  /*右上角登录状态栏样式*/
  .statusColumn{
    position: absolute;
    right: 20px;
    top: 50%;
    z-index: 32132132132;
    transform: translateY(-50%);
    img{
      vertical-align: middle;
      width: 25px;
    }
    span{
      font-size: 14px;
      strong{
        color: #fff;
        font-weight: normal;
      }
    }
    .logout {
      a {
        font-weight: normal;
        color: #fe8283;
        text-decoration: underline;
        margin-left: 15px;
      }
    }
  }
  /*右下角导航样式*/
  .nav{
    overflow: visible;
    zoom: 1;
    position: absolute;
    right:15%;
    bottom:0;
    >li{
      float:left;
      padding:5px 5px 0;
      cursor:pointer;
      .navP{
        border-radius: 5px 5px 0 0;
        padding:20px 5px;
        strong{
          display: inline-block;
          width: 30px;
          height:30px;
          vertical-align:middle;
          margin-right: 10px;
          background: url("../../../assets/main_images/nav.png");
          background-size: 100% auto;
        }
      }
      .navActive{
        color: #fff;
        border-bottom: 3px solid #1f7efe;
        strong{
          width: 30px;
          height:30px;
          background: url("../../../assets/main_images/nav_white.png");
          background-size: 100% auto;
        }
      }
      span{
        color: #fff;
        font-size: 14px;
        vertical-align:middle;
        letter-spacing: 1px;
      }
      .littleNav{
        position: absolute;
        top: 70px;
        left: -11px;
        background: #54637f;
        border: 1px #c5c5c5 solid;
        border-top: 0;
        z-index: 53615361;
        width: calc(100% + 25px);
        border-radius: 0 0 3px 3px;
        .littleNavP{
          margin:4px 0;
          padding:8px 10px;
          border-radius: 0;
          strong{
            display: inline-block;
            width: 20px;
            height:20px;
            vertical-align:middle;
            margin-right: 3px;
            color: #fff;
          }
          span {
            font-size: 12px;
          }
        }
        a.router-link-active>.littleNavP{
          background: #1DA6CE;
          color:#fff;
          strong{
            width: 20px;
            height:20px;
          }
        }
      }
    }
  }
  .f14 {
    font-size: 14px;
  }
  .f15 {
    font-size: 15px;
  }
  .f18 {
    font-size: 18px;
  }
  .mt8 {
    margin-top: -8px;
  }
  .api{
    ul{
      li{
        padding: 5px 0;
        border-bottom: 1px solid #d1d1d1;
        &:last-child{
          border: 0;
        }
      }
    }
  }
}
