<template>
  <div id="header">
    <!--标题图片-->
    <img class="title" src="../../../assets/login_images/title1.png" alt="">
    <!--右上角登录状态栏-->
    <div class="statusColumn">
      <span>
        <Poptip content="content" placement="bottom">
          <img src="../../../assets/main_images/tx.png" alt="">
          <div class="api" slot="content" style="width: 290px">
            <ul>
              <li v-for="(item, index) in listCheck" :key="index">
                {{item.cbd002}}于{{item.cbd003}}{{item.cbd005}}关于{{item.cbd004}}的{{item.cbd006}}
              </li>
            </ul>
          </div>
        </Poptip>
        <strong style="margin-left: 8px">系统管理员</strong>
      </span>
      <span class="logout">
        <router-link to="/login" @click.native="delLogin">
          退出系统
        </router-link>
      </span>
    </div>
    <!--右下角导航-->
    <ul class="nav">
      <li v-for="(item,index) in data" :key="index" style="position: relative" @click="littleNavState=item.meta.id;theIndex = index" ref="nav">
        <router-link :to="item.url" v-show="item.meta.type !== 'out'">
          <p class="navP" :class="{navActive :navActiveState === item.meta.id || littleNavState === item.meta.id}">
            <!--            <strong :style="{backgroundPosition:item.backPosition }"></strong>-->
            <span>{{item.name}}</span>
          </p>
        </router-link>
        <div style="display: inline-block" @click="link(item.url)" v-show="item.meta.type === 'out'">
          <p class="navP" :class="{navActive :navActiveState === item.meta.id || littleNavState === item.meta.id}">
            <!--            <strong :style="{backgroundPosition:item.backPosition }"></strong>-->
            <span>{{item.name}}</span>
          </p>
        </div>
        <div class="littleNav" ref="littleNav" v-show="littleNavState === item.meta.id && item.children.length !== 0">
          <div v-for="(item2,index) in item.children" :key="index" @click.stop="littleNavState = -1;theIndex = -1">
            <router-link :to="item2.url">
              <p class="littleNavP">
                <strong :class="item2.backPosition" class="iconfont"></strong>
                <span>{{item2.name}}</span>
              </p>
            </router-link>
          </div>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
import { listCheckMsg } from '../../../api/login'
export default {
  name: 'Nav',
  data () {
    return {
      data: [
        {
          name: '勘界定标管理',
          url: '#',
          backPosition: '0 150px',
          meta: {
            id: 22
          },
          children: [
            {
              name: '工作底图',
              url: '/main/circlesMark/dataTable',
              backPosition: 'icon-tuceng',
              meta: {
                id: 23
              }
            },
            {
              name: '预设台账',
              url: '/main/circlesMark/inflectionPoint',
              backPosition: 'icon-xinxi',
              meta: {
                id: 24
              }
            },
            {
              name: '拐点提取',
              url: '/main/pointExtract/pointExtract',
              backPosition: 'icon-tiqu mt8',
              meta: {
                id: 25
              }
            }
          ]
        },
        {
          name: '项目准入核查',
          url: '/main/projectApproval/proApp',
          backPosition: '0 120px',
          children: [],
          meta: {
            id: 21
          }
        },
        {
          name: '人类活动遥感监测',
          url: '#',
          backPosition: '0 180px',
          meta: {
            id: 10
          },
          children: [
            {
              name: '人类活动自动识别',
              url: '/main/monitor/automationTable',
              backPosition: 'icon--shuju-zidongshibie f15',
              meta: {
                id: 11
              }
            },
            {
              name: '人类活动判读',
              url: '/main/monitor/extracTable',
              backPosition: 'icon-xinxitiqu',
              meta: {
                id: 12
              }
            },
            {
              name: '人类活动图斑',
              url: '/main/monitor/activitiesInspectTable',
              backPosition: 'icon-jishijihechaxun f18',
              meta: {
                id: 13
              }
            },
            {
              name: '人类活动监管台账',
              url: '/main/monitor/ledgerManageTable',
              backPosition: 'icon-taizhangfenlei',
              meta: {
                id: 14
              }
            },
            {
              name: '人类活动变化检测',
              url: '/main/monitor/changeDetectTable',
              backPosition: 'icon-jiance f18',
              meta: {
                id: 15
              }
            },
            {
              name: '人类活动专题统计',
              url: '/main/monitor/reportAnalysisTable',
              backPosition: 'icon-fenxipingbi',
              meta: {
                id: 16
              }
            },
            {
              name: '人类活动干扰评价',
              url: '/main/monitor/evaluationList',
              backPosition: 'icon-pingjia',
              meta: {
                id: 17
              }
            },
            {
              name: '人类活动变化评价',
              url: '/main/monitor/evaluationList2',
              backPosition: 'icon-pingjia',
              meta: {
                id: 17
              }
            },
            {
              name: '模型参数权重库',
              url: '/main/monitor/WeightLibrary',
              backPosition: 'icon-dianjiacanshu-',
              meta: {
                id: 18
              }
            },
            // 临时添加
            {
              name: '巡查结果质量评估',
              url: '/main/monitor/QualityAssessment',
              backPosition: 'icon-pinggu',
              meta: {
                id: 19
              }
            },
            {
              name: '巡查结果质量控制',
              url: '/main/monitor/QualityControl',
              backPosition: 'icon-kongzhi',
              meta: {
                id: 20
              }
            }
          ]
        },
        {
          name: '核查任务管理',
          url: '#',
          backPosition: '0 90px',
          meta: {
            id: 3
          },
          children: [
            // {
            //   name: '核查影像管理',
            //   url: '/main/dynamicManage/imageManagement',
            //   backPosition: 'icon-gongchengyingxiangguanli',
            //   meta: {
            //     id: 14
            //   }
            // },
            {
              name: '核查任务管理',
              url: '/main/dynamicManage/taskManagementTable',
              backPosition: 'icon-renwuguanli',
              meta: {
                id: 4
              }
            },
            {
              name: '核查任务调度',
              url: '/main/dynamicManage/taskScheduler',
              backPosition: 'icon-renwutiaodu',
              meta: {
                id: 5
              }
            },
            {
              name: '野外工作监管',
              url: '/main/dynamicManage/workRegulation',
              backPosition: 'icon-jianguan f14',
              meta: {
                id: 6
              }
            },
            {
              name: '核查成果审核',
              url: '/main/dynamicManage/achievementTable',
              backPosition: 'icon-shenhe1',
              meta: {
                id: 7
              }
            },
            {
              name: '核查统计分析',
              url: '/main/dynamicManage/dataAnalysis',
              backPosition: 'icon-tongjifenxi',
              meta: {
                id: 8
              }
            },
            {
              name: '核查考勤统计',
              url: '/main/dynamicManage/areaManagement',
              backPosition: 'icon-kaoqin',
              meta: {
                id: 9
              }
            }
          ]
        },
        {
          name: '数据互联互通',
          url: 'http://10.240.25.246:8080/webjshtAPP/#home',
          children: [],
          meta: {
            id: 2,
            type: 'out'
          }
        },
        /*{
          name: '红线信息服务',
          url: '/main/dataCenter',
          children: [],
          meta: {
            id: 1
          }
        },*/
        {
          name: '红线信息服务',
          url: '#',
          backPosition: '0 90px',
          meta: {
            id: 1
          },
          children: [
            {
              name: '红线信息汇总',
              url: '/main/dataCenter',
              backPosition: 'icon-renwuguanli',
              meta: {
                id: 4
              }
            },
            {
              name: '流程定义',
              url: '/main/dataCenterLc',
              backPosition: 'icon-renwuguanli',
              meta: {
                id: 4
              }
            },
            {
              name: '服务产品制作',
              url: '#',
              backPosition: 'icon-renwuguanli',
              meta: {
                id: 4
              }
            },
            {
              name: '红线信息汇总',
              url: '#',
              backPosition: 'icon-renwuguanli',
              meta: {
                id: 4
              }
            }
          ]
        },
        {
          name: '系统管理',
          url: '/main/systemManage/mobileUserManage',
          backPosition: '0 30px',
          meta: {
            id: 18
          },
          children: []
        }
      ],
      /* 路由高亮状态 */
      navActiveState: -1,
      /* 二级路由框展示状态 */
      littleNavState: -1,
      /* 当前点击的导航的index */
      theIndex: -1,
      /* 消息提醒 */
      listCheck: []
    }
  },
  watch: {
    $route (to) {
      this.navActiveState = this.judgeRouter(to.path)
    }
  },
  mounted () {
    this.listCheckMsg()
    document.addEventListener('click', this.littleNavHide)
    this.navActiveState = this.judgeRouter(this.$route.path)
    let arr = []
    if (this.$store.getters.getloginInfor() !== null) {
      for (let item of this.$store.getters.getloginInfor().resource) {
        arr.push(item.id)
      }
    }
    for (let i in this.data) {
      this.data[i].children = this.data[i].children.filter(wes => arr.indexOf(wes.meta.id) !== -1)
    }
    this.data = this.data.filter(wes => arr.indexOf(wes.meta.id) !== -1)
  },
  beforeDestroy () {
    document.removeEventListener('click', this.littleNavHide)
  },
  methods: {
    /* 一级导航router判断 */
    judgeRouter (url) {
      // 红线信息服务
      if (url.indexOf('/main/dataCenter') !== -1) {
        return 1
      }
      // 勘界定标内业管理
      if (url.indexOf('/main/circlesMark') !== -1) {
        return 2
      }
      // 项目准入核查
      if (url.indexOf('/main/projectApproval') !== -1) {
        return 5
      }
      // 人类活动遥感监测
      if (url.indexOf('/main/monitor') !== -1) {
        return 6
      }
      // 核查任务管理
      if (url.indexOf('/main/dynamicManage') !== -1) {
        return 13
      }
      // 系统管理
      if (url.indexOf('/main/systemManage') !== -1) {
        return 21
      }
    },
    /* 点击任意地方隐藏二级导航 */
    littleNavHide (e) {
      if (this.littleNavState !== -1) {
        if ((!this.$refs.littleNav[this.theIndex].contains(e.target)) && (!this.$refs.nav[this.theIndex].contains(e.target))) {
          this.littleNavState = -1
        }
      }
    },
    /* 删除登录信息 */
    delLogin () {
      this.$store.commit('setloginInfor', {})
      sessionStorage.removeItem('loginInfo')
    },
    /* 跳转页面 */
    link (url) {
      window.location.href = url
    },
    /* 获取消息提醒列表 */
    listCheckMsg () {
      listCheckMsg({
        data: {}
      }).then(res => {
        if (res.data.code === '0000') {
          console.log(res.data.data)
          this.listCheck = res.data.data.data
        }
      })
    }
  }
}
</script>

<style lang="scss" rel="stylesheet/scss" scoped>
  #header{
    border-bottom:none ;
    position: relative;
    background: url("../../../assets/main_images/navBack.png") no-repeat;
    background-size: 100% 100%;
    height: 70px;
    /*logo*/
    .title {
      width: 25%;
      position: absolute;
      left: 20px;
      top: 50%;
      transform: translateY(-50%);
    }
    /*右上角登录状态栏样式*/
    .statusColumn{
      position: absolute;
      right: 20px;
      top: 50%;
      z-index: 32132132132;
      transform: translateY(-50%);
      img{
        vertical-align: middle;
        width: 25px;
      }
      span{
        font-size: 14px;
        strong{
          color: #fff;
          font-weight: normal;
        }
      }
      .logout {
        a {
          font-weight: normal;
          color: #fe8283;
          text-decoration: underline;
          margin-left: 15px;
        }
      }
    }
    /*右下角导航样式*/
    .nav{
      overflow: visible;
      zoom: 1;
      position: absolute;
      right:15%;
      bottom:0;
      >li{
        float:left;
        padding:5px 5px 0;
        cursor:pointer;
        .navP{
          border-radius: 5px 5px 0 0;
          padding:20px 5px;
          strong{
            display: inline-block;
            width: 30px;
            height:30px;
            vertical-align:middle;
            margin-right: 10px;
            background: url("../../../assets/main_images/nav.png");
            background-size: 100% auto;
          }
        }
        .navActive{
          color: #fff;
          border-bottom: 3px solid #1f7efe;
          strong{
            width: 30px;
            height:30px;
            background: url("../../../assets/main_images/nav_white.png");
            background-size: 100% auto;
          }
        }
        span{
          color: #fff;
          font-size: 14px;
          vertical-align:middle;
          letter-spacing: 1px;
        }
        .littleNav{
          position: absolute;
          top: 70px;
          left: -11px;
          background: #54637f;
          border: 1px #c5c5c5 solid;
          border-top: 0;
          z-index: 53615361;
          width: calc(100% + 25px);
          border-radius: 0 0 3px 3px;
          .littleNavP{
            margin:4px 0;
            padding:8px 10px;
            border-radius: 0;
            strong{
              display: inline-block;
              width: 20px;
              height:20px;
              vertical-align:middle;
              margin-right: 3px;
              color: #fff;
            }
            span {
              font-size: 12px;
            }
          }
          a.router-link-active>.littleNavP{
            background: #1DA6CE;
            color:#fff;
            strong{
              width: 20px;
              height:20px;
            }
          }
        }
      }
    }
    .f14 {
      font-size: 14px;
    }
    .f15 {
      font-size: 15px;
    }
    .f18 {
      font-size: 18px;
    }
    .mt8 {
      margin-top: -8px;
    }
    .api{
      ul{
        li{
          padding: 5px 0;
          border-bottom: 1px solid #d1d1d1;
          &:last-child{
            border: 0;
          }
        }
      }
    }
  }
</style>
<style>
  .ivu-poptip-popper{
    z-index: 100000;
    top: 40px !important;
  }
</style>
